<template>
  <nav-bar>
    <span slot="title" class="title">数据分析</span>
  </nav-bar>
  <table class="table">
    <tr>
      <td colspan="2">当年月销售量分析</td>
    </tr>
    <tr v-if="chartData.datasets[0].data.length > 0">
      <td rowspan="11">
        <div style="width:90%;height:100%;">
          <product-sales-volume :chart-data="chartData"></product-sales-volume>
        </div>
      </td>
      <td width="200">最高月份</td>
    </tr>
    <tr>
      <td>￥{{maxMonth}}</td>
    </tr>
    <tr>
      <td>最低月份</td>
    </tr>
    <tr>
      <td>￥{{minMonth}}</td>
    </tr>
    <tr>
      <td>月份平均</td>
    </tr>
    <tr>
      <td>￥{{averageByYear}}</td>
    </tr>
    <tr>
      <td>当年总额</td>
    </tr>
    <tr>
      <td>￥{{amountByYear}}</td>
    </tr>
    <tr>
      <td></td>
    </tr>
    <tr>
      <td></td>
    </tr>
  </table>
</template>
<script>
export default {
  data () {
    return {
      chartData: {
        labels: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
        datasets: [
          {
            label: 'My Second dataset',
            fillColor: 'rgba(151,187,205,0.2)',
            strokeColor: 'rgba(151,187,205,1)',
            pointColor: 'rgba(151,187,205,1)',
            pointStrokeColor: '#fff',
            pointHighlightFill: '#fff',
            pointHighlightStroke: 'rgba(151,187,205,1)',
            data: []
          }
        ]
      }
    }
  },
  route: {
    data (transition) {
      this.$http.get('http://jiancan.me/api/m1/shops/statistic.json', { access_token: localStorage.token }).then(function (response) {
        this.$set('chartData.datasets[0].data', response.data.shops)
      }, function (response) {
        this.$dispatch('response-msg', response)
      })
    }
  },
  computed: {
    maxMonth () {
      return Math.max(...this.chartData.datasets[0].data)
    },
    minMonth () {
      return Math.min(...this.chartData.datasets[0].data)
    },
    averageByYear () {
      var length = this.chartData.datasets[0].data.length
      var sum = 0.0
      if (length > 0) {
        for (var x = 0; x < length; x++) {
          sum = sum + Number(this.chartData.datasets[0].data[x])
        }
        return (sum / length).toFixed(2)
      }
      return 0.0
    },
    amountByYear () {
      var length = this.chartData.datasets[0].data.length
      var sum = 0.0
      if (length > 0) {
        for (var x = 0; x < length; x++) {
          sum = sum + Number(this.chartData.datasets[0].data[x])
        }
        return sum
      }
      return 0.0
    }
  },
  components: {
    'nav-bar': require('../components/nav-bar.vue'),
    'product-sales-volume': require('../components/charts/product-sales-volume.vue')
  }
}
</script>



